@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext');

body {
      background: white;
      font-family: 'Source Sans Pro';

}
.App {
    width: 1100px;
    margin: 0 auto;
    display: block;
    max-width: 100%;
}
img.App-logo {
    width: 100%;
}
h2 {
    background: #bd82a4;
    color: white;
    text-align: center;
    margin-top: -4px;
    padding: 9px 0;
    text-transform: uppercase;
}
.group {
    border-radius: 18px;
    -webkit-box-shadow: 3px 5px 13px -1px #b7a8a8;
            box-shadow: 3px 5px 13px -1px #b7a8a8;
    display:none;
}
.Group1 {
  display:block;
}
.Question {
    padding: 13px 38px 13px 38px;
    color: #bd82a4;
}
h3 {
    padding: 00px;
    margin: 0px;
    margin-bottom: 8px;
}
button {
    margin-top: 15px;
    display: block;
    margin-bottom: 24px;
    text-align: center;
    background: #ccb1c0;
    color: white;
    text-transform: uppercase;
    border: none;
    font-size: 17px;
    font-weight: bold;
    padding: 10px 20px;
    width: 100%;
    cursor: pointer;
}

button:hover {
    background: #ad91a0;
}
label {
    margin-right: 15px;
}
.results {
    text-align: center;
}
li.listitem {
    width: 300px;
    margin: 0 auto;
    display: block;
    list-style: none;
    background: #f0f0f0;
    margin-bottom: 5px;
    padding: 8px 0px;
    font-size: 23px;
}
.results h3 {
    width: 92%;
    margin: 0 auto;
    margin-bottom: 30px;
}
@media only screen
and (max-width : 689px) {
/* Styles */
label {
    margin-right: 15px;
    display: block;
    margin-bottom: 6px;
}
}



